'use strict';

import * as styles from 'src/styles/Header.module.css';
import Core from 'src/Core/Core.js';
import React from 'react';
import { Link } from 'gatsby';
import { Helmet } from 'react-helmet';

class Version extends React.Component {
    constructor(props) {
        super(props);

        this.state = {};

        this.state.version = null;

        async function V(data_str, thiss) {
            let data = data_str.split('/');

            let Owner = data[0];
            let Repo = data[1];

            let f = fetch(`https://api.github.com/repos/${Owner}/${Repo}/releases/latest`);

            let d = await (await f).json();

            let version = 'v' + d.tag_name;

            thiss.setState({
                version: version
            });
        };

        V('Kanami-Community/site', this);
    };

    render() {
        return (
            <div className={styles.Version}>
                <h1>
                    {this.state.version}
                </h1>
            </div>
        );
    };
};

class VersionLink extends React.Component {
    constructor(props) {
        super(props);

        this.state = {};

        this.state.version = null;
        this.state.href = null;
        this.state.title = null;

        async function V(data_str, thiss) {
            let data = data_str.split('/');

            let Owner = data[0];
            let Repo = data[1];

            let f = fetch(`https://api.github.com/repos/${Owner}/${Repo}/releases/latest`);

            let d = await (await f).json();

            let version = 'v' + d.tag_name;
            let href = d.html_url;

            thiss.setState({
                version: version,
                href: href,
                title: version
            });
        };

        V('Kanami-Community/site', this);
    };

    render() {
        return (
            <a className={styles.e} href={this.state.href} title={this.state.title}>
                <h1>
                    {this.state.version}
                </h1>
            </a>
        );
    };
};

Version.Link = VersionLink;

class HeaderInfo extends React.Component {
    click() {
        document.location.href = Core.path('/');
    };

    render() {
        return (
            <div className={styles.info} onClick={this.click}>
                <div className={styles.iconWrapper}>
                    <img src={Core.Config.Data.IconURL} alt="" className={styles.icon} />
                </div>
                <h1 className={styles.name}>
                    {Core.Config.Data.Name}
                </h1>
            </div>
        );
    };
};

class HeaderContent extends React.Component {
    constructor(props) {
        super(props);

        this.click = this.click.bind(this);

        this.state = {};

        this.state.state = null;

        this.state.state = 'close';
    };

    click() {
        if (this.state.state === 'close') {
            this.setState({
                state: 'open'
            });
        } else if (this.state.state === 'open') {
            this.setState({
                state: 'close'
            });
        };
    };

    render() {
        return (
            <div className={styles.content}>
                <div className={styles.contentBtn} onClick={this.click} state={this.state.state}>
                    <div className={styles.one}></div>
                    <div className={styles.two}></div>
                    <div className={styles.three}></div>
                </div>
                <div className={styles.contentContent} state={this.state.state}>
                    <nav>
                        {this.props.children}
                    </nav>
                </div>
            </div>
        );
    };
};

class HeaderContentItem extends React.Component {
    render() {
        return (
            <Link className={styles.e} to={this.props.href} title={this.props.title}>
                <h1>
                    {this.props.children}
                </h1>
            </Link>
        );
    };
};

class Header extends React.Component {
    render() {
        return (
            <div className={styles.Header}>
                <HeaderInfo />
                <Version />
                <HeaderContent>
                    <HeaderContentItem href="/" title=" ">
                        
                    </HeaderContentItem>
                    <a className={styles.e} href="https://kanami-community.github.io/docs/" title=" ">
                        <h1>
                            
                        </h1>
                    </a>
                    <HeaderContentItem href="/developers/" title="">
                        Devs
                    </HeaderContentItem>
                    <HeaderContentItem href="/features/" title=" ">
                        
                    </HeaderContentItem>
                    <Version.Link />
                </HeaderContent>
                <Helmet>
                    {this.props.children}
                </Helmet>
            </div>
        );
    };
};

export default Header;